
import XButton from 'vux/src/components/x-button/index.vue'

import Vue from 'vue'
export default {
name: 'HelloWorld',
data () {
return {
count: 3
}
},
components: {
XButton
},
computed: {

},
mounted: {
skip () {
this.$router.push('/home')
}
},
mounted () {
let timer = setInterval(() => {
if (this.count == 0) {
clearInterval(timer)
timer = null
this.$router.push('/home')
return
}
this.count --
}, 1000)
}
}



